html,
body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #ffffff;
    color: rgb(26, 25, 25);
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.container {
    width: 92%;
    max-width: 1100px;
    margin: auto;
    margin-top: 30vh;
    overflow: hidden;
}

@media (max-width:760px) {
    .container {
        margin-top: 22vh;
    }
}